body {
  margin: 0;
  background-color: rgb(116, 219, 219);
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-size{
  font-size: larger;
}

.img-size{
  width: 100%;
  height: 530px;
}

.top{
  text-align: center;
  margin: 50px;
}

.width{
  width: 200px;
}

.img{
  height : 150px;
  width: 100%;
}

.fav-width{
  width: 5%;
}

.nav-bg{
  background-color: rgb(104, 91, 91);
}

.wrapper{
  position: relative;
}

.cart-item{
  position :absolute;
  top: -2px;
  right: -2px; 
}